@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
:root {
  --color-primary: #61DBFB;
  --color-primary-accent: #8CCEE3;
  --color-primary-shade: #6BA3CE;

  --color-success: #58be81;
  --color-danger: #f44336;
  --color-default: #999999;

  --color-dark: #555;
  --color-gray: #83A3B0;
  --color-light: #F8F7F5;

  --color-light-shade: #fff;
}

* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #6BA3CE;
  background-color: var(--color-primary-shade);
  color: #555;
  color: var(--color-dark);
}


.expenses-filter {
    color: var(--color-light);
    padding: 0 1rem;
}

.expenses-filter__control {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
}

.expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    flex: 20 1 10rem;
}

.expenses-filter .select-wrapper {
    position: relative;
    flex: 1 1 auto;
}

.select-wrapper::after {
    content: '▼';
    font-size: .75em;
    top: 29%;
    right: 1rem;
    position: absolute;
    color: black;
}

.expenses-filter select {
    font: inherit;
    padding: 0.5rem 1.75rem .5rem 1rem;
    font-weight: bold;
    border-radius: 6px;
    border: 1px solid rgba(0 0 0 / 20%);
    
    width: 100%;

    -webkit-appearance: none;

            appearance: none;
}

.expense-date {
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    background-color: var(--color-light);
    color: var(--color-dark);
    border-radius: 12px;
    align-items: center;
    justify-content: center;
  }
  
  .expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .expense-date__year {
    font-size: 0.75rem;
  }
  
  .expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }
.card {
    border-radius: 5px;
    box-shadow: 0 1px 3px 1px rgba(0 0 0 / 12%);
}
.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 2rem;
    margin: 1rem 0;
    color: var(--color-dark);
    background-color: var(--color-light-shade);
}

.expense-item__description {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1 1;
}

.expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1 1;
    margin: 0 1rem;
    text-align: right;
}

.expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: var(--color-dark);
    padding: 0.5rem;
    border: none;
}

@media (min-width: 580px) {
    .expense-item__description {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1 1;
    }

    .expense-item__description h2 {
        font-size: 1.25rem;
    }

    .expense-item__price {
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
        border-left: 2px solid var(--color-primary-shade);

    }
}

@media (max-width: 425px) {
    .expense-item {
        flex-direction: column;
    }

    .expense-item > * {
        width: 100%;
    }

    .expense-date {
        flex-direction: row;
        grid-gap: .5rem;
        gap: .5rem;
        height: auto;
        padding-block: .5em;
        line-height: 1;
        align-items: baseline;
    }

    .expense-item__price {
        width: 100%;
        margin-top: .5rem;
        text-align: center;
    }

    .expense-item__description {
        align-items: center;
    }
}

.expenses-list {
    list-style: none;
    padding: 0;
}

.expenses-list__fallback {
    color: var(--color-light);
    text-align: center;
}

.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.chart-bar__inner {
    height: 100%;
    width: 100%;
    border: 1px solid rgba(0 0 0 / 12.5%);
    background-color: var(--color-light);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.chart-bar__fill {
    background-color: var(--color-primary-shade);
    width: 100%;
    transition: all 0.3s ease-out;
}

.chart-bar__label {
    font-size: 0.5rem;
    color: #000000cc;
}

.chart {
    padding: 1rem;
    border-radius: 5px;
    background-color: var(--color-light-shade);
    display: flex;
    justify-content: space-around;
    height: 10rem;
}

.expenses {
    padding: 1rem;
    background-color: var(--color-light);
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
}

.new-expense {
    background-color: var(--color-light);
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 5px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }
  
  .new-expense button {
    font: inherit;
    cursor: pointer;
    padding: 0.5em 1em;
    color: white;
    border-radius: 5px;
    border: none;
  }
  
  .new-expense button:hover,
  .new-expense button:active {
    background-color: var(--color-dark);
    border-color: var(--color-dark);
  }
  
  .new-expense button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
  }
  

  @media (max-width: 342px) {
    .new-expense__actions > button {
      display: block;
      width: 100%;
    }
  }
.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }

  .new-expense__control:first-child {
    flex: 2 1 30rem;
  }

  .new-expense__control {
    flex: 1 1 15rem;
  }

  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5em;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 100%;
  }
  
  .new-expense__actions {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    grid-gap: .75rem;
    gap: .75rem;
    margin-top: 1.75rem;
  }

  .btn-primary {
    background-color: var(--color-primary-shade);
  }

  .btn-danger {
    background-color: var(--color-danger);
  }

  .btn-success {
    background-color: var(--color-success);
  }

  @media (max-width: 342px) {
    .new-expense__actions {
      flex-direction: column-reverse;
    }
  }

